// ------------------
//  header hero block
// ------------------
header {
    position: relative;
    width: 100%;
  
    /* ① Tell the browser the picture’s real aspect-ratio.      */
    /*    (Replace 1920 / 1080 with your own WIDTH / HEIGHT.)    */
    aspect-ratio: 1920 / 1080;
  
    /* ② Never grow taller than the visible viewport.            */
    max-height: 100vh;          /* optional but avoids over-scrolls */
  
    /* ③ Show the *whole* image, never crop.                     */
    background-image: url('../img/header.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;   /* <-- the key change            */
  
    /* keep the rest of your styling */
    text-align: center;
    color: white;
    .header-content {
        /* was: position: relative; */
        position: absolute;         // lift it off the normal flow
    
        top: 50%; 
        left: 0;                 // stick to the left edge
        right: 0;                // and the right edge
        transform: translateY(-50%);   // vertical centring only
    
        text-align: center;
        padding: 0 15px;            // keep a little side-padding; drop the 100 px spacers
        width: 100%;
    
        .header-content-inner {
            h1 {
                font-weight: 700;
                text-transform: uppercase;
                margin-top: 0;
                margin-bottom: 0;
            }
            hr {
                margin: 30px auto;
            }
            p {
                font-weight: 300;
                color: rgba(white, .7);
                font-size: 16px;
                margin-bottom: 50px;
            }
        }
    }
  }
  
  /* 2 – One-liner fallback for older browsers that lack aspect-ratio */
  @supports not (aspect-ratio: 1) {
    header {
      height: 0;
      /* 1080 ÷ 1920 × 100%  =  56.25% (16 : 9) */
      padding-top: 56.25%;      /* same ratio as your image */
    }
  }
  
