#emailLink {
    color: inherit;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    margin-left: 12px; // Adjust the left margin to move the switch away from the corner
    margin-top: 8px;
    input { 
        opacity: 0;
        width: 0;
        height: 0;
    }

    .slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #7e7e7e; // Default color (gray)
        transition: .4s;

        &:before {
            position: absolute;
            content: "";
            height: 26px;
            width: 26px;
            left: 4px;
            bottom: 4px;
            background-color: rgb(43, 43, 43); // Knob color
            transition: .4s;
        }
    }

    input:checked + .slider {
        background-color: rgb(164, 164, 164); // Color when checked (gray)
    }

    input:checked + .slider:before {
        transform: translateX(26px);
    }

    // Rounded sliders
    .slider.round {
        border-radius: 34px;

        &:before {
            border-radius: 50%;
        }
    }
}

#languageLabel {
    margin-right: 20px; /* Space between the toggle and the text */
    margin-left: 3px;
    font-size: 18px; /* Adjust font size as needed */
    color: rgb(211, 211, 211);
    @include transition-all;
}

